html {
  font-size: 10px;
}
body {
  font-size: 1.6rem;
  font-family: 'Arial';
  color: #666666;
  text-rendering: optimizeLegibility;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-style: normal;
  font-weight: 300;
}
.btn {
  border: 0;
  background-color: none;
  outline: 0;
}
.container {
  max-width: 980px;
  margin: 0 auto;
}
.anchor {
  display: none;
}
.main-nav {
  background-color: #009999;
  padding: 20px 25px;
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
}
.main-logo {
  display: inline;
  float: left;
  width: 25%;
  margin: 0 0%;
  max-width: 170px;
  text-align: center;
}
.main-logo i {
  display: inline-block;
  font-size: 3.6rem;
  color: #ffffff;
  margin-right: 15px;
}
.main-logo-title {
  display: inline;
  font-size: 3.6rem;
  color: #ffffff;
  margin: 0;
}
.main-search {
  display: inline;
  float: left;
  width: 33.33333333%;
  margin: 0 0%;
  position: relative;
}
.custom-search {
  position: relative;
}
.custom-search:after {
  content: "\f002";
  font-family: "FontAwesome";
  position: absolute;
  font-size: 2.6rem;
  display: inline-block;
  top: 10px;
  left: 30px;
}
.search {
  display: block;
  width: 100%;
  height: 40px;
  outline: 0;
  margin-left: 15px;
  border-radius: 12px;
  padding-left: 50px;
  padding-right: 20px;
  border: 1px solid #404040;
}
.main-menu {
  display: inline;
  float: left;
  width: 41.66666667%;
  margin: 0 0%;
  float: right;
  padding-left: 0;
  margin-top: 12px;
  text-align: center;
}
.main-menu a {
  text-decoration: none;
}
.main-menu a:last-child li {
  border-right: 0;
}
.main-menu-item {
  color: #ffffff;
  font-size: 1.8rem;
  display: inline-block;
  padding: 0 25px;
  border-right: 1px solid #ffffff;
}
.main-section {
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
  margin: 45px 0;
}
.main-section-title {
  font-size: 3.6rem;
  font-weight: 700;
  text-align: center;
  color: #333333;
}
.cl-one {
  color: #0099cc;
  margin-right: -10px;
}
.cl-two {
  color: #993366;
  margin-right: -10px;
}
.cl-three {
  color: #666666;
  margin-right: -10px;
}
.section-text {
  font-size: 2.2rem;
  max-width: 775px;
  margin: 0 auto;
  margin-top: 60px;
}
@media (max-width: 768px) {
  .anchor {
    display: block;
    color: #009999;
    font-size: 3.6rem;
    position: fixed;
    right: 15px;
    top: 15px;
    cursor: pointer;
  }
  .main-nav {
    position: fixed;
    height: 100%;
    transform: translateX(-768px);
    transition: 0.3s all ease-in-out;
  }
  .main-logo {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
    max-width: 100%;
  }
  .main-search {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
    max-width: 100%;
  }
  .custom-menu {
    width: 80%;
    margin: 0 auto;
  }
  .search {
    display: block;
    margin: 0 auto;
    max-width: 80%;
  }
  .custom-search {
    margin: 20px auto;
  }
  .main-menu {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
  }
  .menu-active .main-nav {
    transform: translateX(0);
  }
  .main-menu-item {
    display: block;
    border-right: 0;
    padding: 20px 0;
  }
  .section-text {
    text-align: center;
  }
}
.ades-title {
  *zoom: 1;
  width: 100%;
  text-align: center;
  position: relative;
  clear: both;
  background-color: #fff;
}
.ades-title:before,
.ades-title:after {
  content: "";
  display: table;
}
.ades-title:after {
  clear: both;
}
.ades-title:after {
  content: "";
  width: 100%;
  height: 1px;
  display: inline-block;
  position: absolute;
  left: 0;
  top: 15px;
  z-index: -1;
  border-top: 2px dashed #404040;
}
.ades-title-box {
  width: 100px;
  height: 30px;
  background-color: #fff;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
}
.about-box {
  padding-top: 50px;
  display: block;
  width: 100%;
  margin: 0 0%;
  *zoom: 1;
}
.about-box:before,
.about-box:after {
  content: "";
  display: table;
}
.about-box:after {
  clear: both;
}
.about-characteristics {
  display: inline;
  float: left;
  width: 50%;
  margin: 0 0%;
  padding-right: 50px;
}
.about-benefits {
  display: inline;
  float: left;
  width: 50%;
  margin: 0 0%;
  padding-left: 50px;
}
.about-title,
.fundamentals-title,
.mec-title {
  font-size: 3.6rem;
  color: #333333;
  margin-bottom: 40px;
}
.about-list {
  padding-left: 0;
  list-style: none;
}
.about-list li {
  margin-bottom: 25px;
}
.about-list h1 {
  font-size: 1.8rem;
  color: #000000;
  margin: 0;
  padding-left: 45px;
}
.about-list p {
  margin: 0;
  padding-left: 45px;
  font-size: 11px;
  color: #000000;
}
.about-list a {
  padding-left: 15px;
  font-size: 1.1rem;
  color: #ff6600;
}
.about-list a:before {
  content: ">";
  margin-right: 5px;
}
.characteristics-item:before {
  content: "\f00c";
  font-family: "FontAwesome";
  float: left;
  font-size: 3.2rem;
  line-height: 50px;
  color: #0099FF;
}
.benefits-item:before {
  content: "\f005";
  font-family: "FontAwesome";
  float: left;
  display: inline-block;
  font-size: 3.2rem;
  line-height: 50px;
  color: #993366;
}
@media (max-width: 768px) {
  .about-characteristics,
  .about-benefits {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
    padding: 0 15px;
  }
}
.fundamentals-box {
  display: block;
  width: 100%;
  margin: 0 0%;
  *zoom: 1;
  margin-top: 65px;
}
.fundamentals-box:before,
.fundamentals-box:after {
  content: "";
  display: table;
}
.fundamentals-box:after {
  clear: both;
}
.fundamentals-box header {
  margin-bottom: 40px;
}
.fundamentals-subtitle {
  text-align: center;
  font-size: 1.2rem;
  color: #666666;
  font-weight: 700;
  margin-bottom: 0px;
}
.fundamentals-title,
.mec-title {
  text-align: center;
  max-width: 452px;
  margin: 0 auto;
}
.fundamentals-book {
  display: inline;
  float: left;
  width: 25%;
  margin: 0 0%;
  padding-right: 25px;
}
.fundamentals-book span {
  font-size: 1.4rem;
  color: #000000;
}
.book-icon {
  display: inline-block;
  width: 190px;
  height: 290px;
  background-color: red;
}
.book-description {
  display: block;
  margin: 35px 0 15px 0;
  font-size: 1.2rem;
  color: #000000;
}
.btn-download {
  font-size: 1.6rem;
  padding: 10px 15px;
  margin-top: 40px;
  border-radius: 4px;
  font-weight: 700;
  color: #ffffff;
  background: #019b06;
  /* Old browsers */
  background: -moz-linear-gradient(top, #019b06 0%, #0aac3e 60%);
  /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #019b06), color-stop(60%, #0aac3e));
  /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, #019b06 0%, #0aac3e 60%);
  /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(top, #019b06 0%, #0aac3e 60%);
  /* Opera 11.10+ */
  background: -ms-linear-gradient(top, #019b06 0%, #0aac3e 60%);
  /* IE10+ */
  background: linear-gradient(to bottom, #019b06 0%, #0aac3e 60%);
  /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#019b06', endColorstr='#0aac3e', GradientType=0);
  /* IE6-9 */
}
.fundamentals-detail {
  display: inline;
  float: left;
  width: 66.66666667%;
  margin: 0 0%;
  margin-bottom: 40px;
}
.detail-title {
  color: #000000;
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 0;
}
.detail-text {
  color: 1.4rem;
  color: #333333;
  margin-top: 0;
  margin-bottom: 10px;
}
.detail-list {
  list-style: none;
  padding-left: 0;
}
.detail-item,
.detail-item-two {
  display: inline;
  float: left;
  width: 33.33333333%;
  margin: 0 0%;
  padding: 0 5px;
  display: inline-block;
}
.detail-item:before {
  content: "\f071";
  font-family: "FontAwesome";
  font-size: 2.4rem;
  float: left;
  margin-right: 10px;
  color: #FF9900;
}
.detail-item div {
  width: 80%;
  float: left;
}
.detail-item h1 {
  font-size: 1.2rem;
  font-weight: 700;
  color: #000000;
  margin: 0;
}
.detail-item p {
  font-size: 1.2rem;
  color: #000000;
  margin: 0;
}
.detail-item-two:before {
  content: "\f058";
  font-family: "FontAwesome";
  font-size: 2.4rem;
  float: left;
  margin-right: 10px;
  color: #006600;
}
.detail-item-two div {
  width: 80%;
  float: left;
}
.detail-item-two h1 {
  font-size: 1.2rem;
  font-weight: 700;
  color: #000000;
  margin: 0;
}
.detail-item-two p {
  font-size: 1.2rem;
  color: #000000;
  margin: 0;
}
@media (max-width: 768px) {
  .fundamentals-book {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
    padding: 0 15px;
  }
  .fundamentals-book span {
    display: block;
    text-align: center;
  }
  .fundamentals-book .btn-download {
    display: block;
    margin: 40px auto;
  }
  .book-icon {
    display: block;
    margin: 0 auto;
  }
  .book-description {
    text-align: center;
  }
  .fundamentals-detail {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
    padding: 0 15px;
  }
}
@media (max-width: 380px) {
  .detail-item,
  .detail-item-two {
    display: inline;
    float: left;
    width: 100%;
    margin: 0 0%;
    margin: 10px 0;
  }
}
.mec-section {
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
}
.mec-title {
  text-align: center;
}
.mec {
  margin-top: 70px;
}
.mec-detail {
  display: inline;
  float: left;
  width: 58.33333333%;
  margin: 0 0%;
  margin-top: 60px;
}
.mec-detail-text {
  font-size: 1.8rem;
  color: #333333;
  margin-bottom: 0;
}
.mec-subtitle {
  font-size: 1.8rem;
  font-weight: 700;
  color: #000000;
  margin: 0;
  margin-top: 25px;
}
.mac-text {
  font-size: 1.4rem;
  color: #333333;
  margin-top: 0;
  margin-bottom: 10px;
}
.mec-articles-list {
  list-style: none;
  padding-left: 0;
  margin-top: 30px;
  border-left: 10px solid #0099CC;
}
.mec-articles-item {
  font-size: 2rem;
  padding: 0 15px 15px 15px;
}
.link {
  font-size: 1.1rem;
  color: #ff6600;
  text-decoration: none;
}
.howto-section {
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
}
.howto {
  display: inline;
  float: left;
  width: 100%;
  margin: 0 0%;
}
.howto-title {
  font-size: 3.6rem;
  text-align: center;
  color: #333333;
  margin-top: 50px;
